<template>
  <div class="addlabelbox">
    <el-dialog
      title="音频录制"
      :visible.sync="isAudioRecording"
      :modal="false"
      :show-close="false"
      :closeOnClickModal="false"
    >
      <div class="vieoRecord">
        <div class="diagold"></div>
        <div>财经委员会第一次会议在西昌召开 全州文化旅游市场整治行动电视电话会议召开 全州医疗保障工作会议召开，财经委员会第一次会议在西昌召开 全州文化旅游市场整治行动电视电话会议召开 全州医疗保障工作会议召开 ，财经委员会第一次会议在西昌召开</div>
        <div class="contro">
          <button @click="handleClick">打开录音</button>
          <!-- <button @click="handelEndRecord">结束录音</button> -->
        </div>
        <span>{{tiptext}}</span>
        <audio :src="url" controls></audio>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleAddSure('ruleForm')">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { MediaStreamRecorder } from "./MediaStreamRecorder.js";
export default {
  name: "MRecorder",
  props: {
    // timelimit: {
    //   default: 60, // 默认为1min
    //   type: Number
    // },
    // pushurl: {
    //   default: "", // 上传到服务器的路径
    //   type: String
    // }
    // isAudioRecording: {
    //   type: Boolean
    // }
  },
  data() {
    return {
      isAudioRecording: true,
      timelimit: 60,
      url: "",
      mcounter: 0, // 累积时间
      recording: false, // 标记是否在录音
      intervaltimerid: "", // 间隔时间定时器编号
      tiptext: "点击录音", // 提示文字
      mediaRecorder: null, // 录音笔
      mediaConstraints: {
        audio: true
      }
    };
  },
  methods: {
    // 处理点击
    handleClick() {
      var that = this;
      this.recording = !this.recording;
      console.log(this.recording);
      // 如果开始录音
      if (this.recording === true) {
        this.mcounter = 0;
        this.tiptext = "录音中 " + this.mcounter + "s";
        this.captureRecord();
        this.intervaltimerid = setInterval(() => {
          // 开始累积
          that.mcounter = that.mcounter + 1;
          this.tiptext = "录音中 " + that.mcounter + "s";
          //   console.log(this.mediaRecorder);
        }, 1000);
      } else {
        // 如果结束录音
        this.tiptext = "点击录音";
        this.mediaRecorder.stop();
        this.mediaRecorder.stream.stop();
      }
    },
    // 错误处理方法
    onMediaError(e) {
      console.log("阿偶~您的浏览器貌似不支持录音哦...", e);
      clearInterval(this.intervaltimerid);
      this.tiptext = "点击录音";
      this.recording = false;
      this.$message.error("您的浏览器暂不支持录音功能");
    },
    // 成功
    onMediaSuccess(stream) {
      console.log(stream);
      this.mediaRecorder = new MediaStreamRecorder(stream);
      // 获取音频流
      this.mediaRecorder.stream = stream;
      this.mediaRecorder.mimeType = "audio/wav";
      var _that = this;
      this.mediaRecorder.ondataavailable = function(blob) {
        clearInterval(_that.intervaltimerid);
        const url = URL.createObjectURL(blob);
        console.log(url);
        _that.handelEndRecord(url);
      };
      // 定义间隔
      this.mediaRecorder.start(this.timelimit * 1000);
    },
    // 开始记录方法
    captureRecord() {
      navigator.mediaDevices
        .getUserMedia(this.mediaConstraints)
        .then(this.onMediaSuccess)
        .catch(this.onMediaError);
    },
    linit() {
      navigator.getUserMedia =
        navigator.getUserMedia ||
        navigator.webkitGetUserMedia ||
        navigator.mozGetUserMedia;
      function getStreamSuccess(stream) {
        console.log(".......");
      }
      function getStreamError(stream) {}

      if (navigator.getUserMedia) {
      }
    },
    handelEndRecord(param) {
      console.log(param);
      this.url = param;
    },

    onMediaSuccessl(stream) {
      var mediaRecorder = new MediaStreamRecorder(stream);
      mediaRecorder.mimeType = "video/wav";
      mediaRecorder.ondataavailable = function(blob) {
        // POST/PUT "Blob" using FormData/XHR2
        var blobURL = URL.createObjectURL(blob);
        document.write(
          '&lt;a href="' + blobURL + '"&gt;' + blobURL + "&lt;/a&gt;"
        );
      };
      mediaRecorder.start(3000);
    },

    onMediaError1(e) {
      console.error("media error", e);
    }
  }
};
</script>
<style >
</style>



