import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import videoRecord from '@/components/videoRecord'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'videoRecord',
      component: videoRecord
    },
    {
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



